.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
*{
  margin: 0;
  padding: 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
*{
  margin: 0;
  padding: 0;
}
.App-input{
  background-color:darkseagreen;
  text-align: center;
  height: 300px;
  
}
h1{
  color: crimson;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  
}
.App-input input{
  border-radius: 5px;
  padding: 10px;
  border: 1px solid grey;
  outline: none;
}
.App-input button{
  padding: 10px 20px;
  border-radius: 5px;
  border: 1px solid grey;
  cursor: pointer;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  background-color: darksalmon;
  height: 50px;
}
nav ul li{
  font-size: 20px;
  text-align: center;
  cursor: pointer;
  color: white;
  transition:all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.4);
}
.navActive{
  color: blue;
}
.App-container{
  background-color: darkblue;
  color: white;
  padding:10px;

}
